import React, { Component } from 'react'
import { Panel, PanelHeader, PanelHeaderBack, RichCell, SimpleCell, Link, Button, Alert, ScreenSpinner, Div, Avatar, Switch } from '@vkontakte/vkui'
import Icon28SettingsOutline from '@vkontakte/icons/dist/28/settings_outline';
import Icon28ViewOutline from '@vkontakte/icons/dist/28/view_outline';
import Icon28MailOutline from '@vkontakte/icons/dist/28/mail_outline';
import Icon28AddSquareOutline from '@vkontakte/icons/dist/28/add_square_outline';
import Icon28CheckSquareOutline from '@vkontakte/icons/dist/28/check_square_outline';
import bridge from '@vkontakte/vk-bridge'
import SubscribeCities from '../components/SubscribeCities'

import { VKMiniAppAPI } from '@vkontakte/vk-mini-apps-api'



export class Catalog extends Component {

  state = {
    subscribeGroup: {},
    allowMessages:{},
  }
  // Подписка на сообщество
  subscribe = e => {
    const screenName = e.currentTarget.id.split("com/")[1]
    const { state } = this.props
    bridge.sendPromise("VKWebAppGetAuthToken", {
      app_id: state.appId,
      scope: "wall,groups",
      request_id: 1
    })
    .then(auth => {
      if(auth.access_token && auth.scope === "wall,groups"){
        bridge.sendPromise("VKWebAppCallAPIMethod", {
          method: "groups.getById",
          request_id: 2,
          params: {
            group_id: screenName,
            access_token: auth.access_token,
            v: 5.107,
          }
        })
        .then(data => {
          bridge.sendPromise("VKWebAppJoinGroup", {"group_id": data.response[0].id})
          .then(() => {
            // Если пользователь подписался, то отправляем запрос 
            // на сохранение этих данных
            fetch("https://cors-anywhere.herokuapp.com/https://appvk.flights.ru/save-groups", {
              "headers": {
                "accept": "*/*",
                "accept-language": "ru-RU,ru;q=0.9,en-US;q=0.8,en;q=0.7",
                "content-type": "application/json;charset=utf-8",
              },
              "body": JSON.stringify({
                user_id: state.user.id,
                app_id: state.appId,
                checkbox: data.response[0].id
              }),
              "method": "POST",
            });
          })
        })
      }
    })
  }

  componentDidMount() {
    let list = this.props.state.catalog[this.props.state.currentCatalog].to.map((item) => item.code).join(',');
    bridge.sendPromise("VKWebAppGetAuthToken", {
      app_id: this.props.state.appId,
      scope:"",
      request_id: 1
    })
    .then((data) => {
      bridge.send("VKWebAppCallAPIMethod", {"method": "execute.getSubscribeGroup", "params": {"list": list, "v": "5.120", "access_token":data.access_token}})
      .then((response) => {
        let obj = {};
        response.response.forEach((el) => { 
          obj[el.name] = (el.is == 1);
        })
        console.log(obj)
        this.setState({subscribeGroup: obj});
      });

    })






  }

  // Запрос на сообщения
  subscribeMessages = (screenName, name) => {
    const { state } = this.props
    this.props.setPopout(<ScreenSpinner />)
    bridge.sendPromise("VKWebAppGetAuthToken", {
      app_id: state.appId,
      scope: "wall,groups",
      request_id: 1
    })
    .then(auth => {
      if(auth.access_token && auth.scope === "wall,groups"){
        bridge.sendPromise("VKWebAppCallAPIMethod", {
          method: "groups.getById",
          request_id: 2,
          params: {
            group_id: screenName,
            access_token: auth.access_token,
            v: 5.107,
          }
        })
        .then(data => {
          bridge.sendPromise("VKWebAppAllowMessagesFromGroup", {"group_id": data.response[0].id, "key": "dBuBKe1kFcdemzB"})
          .then(() => {
            bridge.sendPromise("VKWebAppCallAPIMethod", {
              method: "wall.get",
              request_id: 2,
              params: {
                domain: screenName,
                access_token: auth.access_token,
                v: 5.107,
              }
            })
            .then(async tags => {
              let tagsIn = [];
              let tagsOut = [];
              let itemsWall = tags.response.items;
              itemsWall.forEach((group) => {
                let match = [...group.text.matchAll(/(#.*@.*)/g)];
                if (match.length) {
                  if (match.length > 2) {
                    let matchOut = match[2];
                    tagsOut.push(matchOut)
                }
                let matchIn = match[0];
                tagsIn.push(matchIn)
                }
              });
              tagsIn = tagsIn.map((item) => 
                item[0]
              )
              tagsIn = tagsIn.filter((v,i) => tagsIn.indexOf(v) === i)
              tagsOut = tagsOut.map((item) => 
                item[0]
              )
              tagsOut = tagsOut.filter((v,i) => tagsOut.indexOf(v) === i)
              let obj = {
                name:name,
                id:null,
                groupId:data.response[0].id, 
                from:tagsOut.map((item) => {
                  return {
                    tag: item,
                    checked: false
                  }
                }),
                to:tagsIn.map((item) => {
                  return {
                    tag: item,
                    checked: false
                  }
                })

              }
              this.props.onChangeGroups('obj', obj);
              this.props.openModal('subscribeCities')
              this.props.setPopout(null) 
              
            })
            .catch(() => this.props.setPopout(null))
          })
          .catch(() => this.props.setPopout(null))
        })
        .catch(() => this.props.setPopout(null))
      }
    })
    .catch(() => this.props.setPopout(null))
  }

  // Предупреждение с информацией
  subscribeMessagesAlert = e => {
    const screenName = e.currentTarget.id.split("com/")[1]
    const name = e.currentTarget.name
    this.props.setPopout(
      <Alert
        actions={[{
          title: 'Отмена',
          autoclose: true,
          mode: 'cancel'
        }, {
          title: 'Разрешить',
          autoclose: true,
          action: () => this.subscribeMessages(screenName, name),
        }]}
        onClose={() => this.props.setPopout(null)}
      >
        <p>Вы можете получать со стены сообщества в личные сообщения только те авиабилеты, которые Вам интересны. Для этого разрешите сейчас сообщения в сообществе и выберите города прилета из списка.</p>
      </Alert>
    )
  }

  render() {
    const {
      id,
      state,
      go,
    } = this.props
    return (
      <Panel id={id}>
        <PanelHeader
          left={
            <PanelHeaderBack onClick={() => go("groups")} />
          }
        >{state.catalog[state.currentCatalog].catalog}</PanelHeader>
        {
          state.catalog && (
            state.catalog[state.currentCatalog].to.map((item, index) => 
              item.section ? 
                <SimpleCell
                  key={index}
                  expandable
                  id={index}
                  onClick={this.props.chooseSection}
                >
                  <Link>{item.section}</Link>
                </SimpleCell>
              :
              <SimpleCell
                key={index}
                multiline
                description={state.my_groups.indexOf(item.code) !== -1 ? "Подписка активна" : "Подписка не активна"}
                onClick={() => this.props.goGroup({...item, source:'catalog'})}
                after={<Avatar size={10} style={ this.props.state.my_groups.indexOf(item.code) !== -1 ? { backgroundColor: 'var(--dynamic_green)' } : { backgroundColor: 'var(--dynamic_red)'}}/>}
              >
                {item.description}
              </SimpleCell>
            )
          )
        }
      </Panel>
    )
  }
}

export default Catalog
